/*  adapted from node-shuffle 
    https://github.com/codegard1/node-shuffle.git */

class Deck {
    constructor(cards, random) {
        this.cards = cards || [];
        this.random = random;
        this.reset();
        this.shuffle();
    }
    reset() {
        this.cards = this.cards.slice(0);
        this.length = this.cards.length;
    }
    shuffle() {
        this.fisherYates(this.cards);
    }
    deal(numberOfCards, arrayOfHands) {
        for (var i = 0; i < numberOfCards; i++)
            for (var j = 0; j < arrayOfHands.length; j++)
                arrayOfHands[j].push(this.cards.pop());
        this.length = this.cards.length;
    }
    draw(num) {
        if (!num || num <= 1) {
            this.length = this.cards.length - 1;
            return this.cards.pop();
        }

        var ret = [];
        for (var i = 0; i < num; i++) ret.push(this.cards.pop());
        this.length = this.cards.length;
        return ret;
    }
    drawFromBottomOfDeck(num) {
        if (!num || num < 1) {
            num = 1;
        }

        var ret = [];
        for (var i = 0; i < num; i++) {
            ret.push(this.cards.shift());
        }
        this.length = this.cards.length;

        if (ret.length === 1) {
            return ret[0];
        } else {
            return ret;
        }
    }
    drawRandom(num) {
        var _draw = function () {
            var index = Math.floor(this.random() * this.cards.length);
            var card = this.cards[index];
            this.cards.splice(index, 1);
            this.length = this.cards.length;
            return card;
        };

        if (!num || num <= 1) {
            return _draw.apply(this);
        } else {
            var cards = [];
            for (var i = 0; i < num; i++) {
                cards.push(_draw.apply(this));
            }
            return cards;
        }
    }
    putOnTopOfDeck(cards) {
        if (!cards instanceof Array) this.cards.push(cards);
        else for (var i = 0; i < cards.length; i++) this.cards.push(cards[i]);
        this.length = this.cards.length;
    }
    putOnBottomOfDeck(cards) {
        if (!cards instanceof Array) this.cards.unshift(cards);
        else for (var i = 0; i < cards.length; i++) this.cards.unshift(cards[i]);
        this.length = this.cards.length;
    }
    //array shuffling algorithm: http://en.wikipedia.org/wiki/Fisher%E2%80%93Yates_shuffle
    fisherYates(arr) {
        var i = arr.length;
        if (i === 0) return false;
        while (--i) {
            var j = Math.floor(this.random() * (i + 1));
            var tempi = arr[i];
            var tempj = arr[j];
            arr[i] = tempj;
            arr[j] = tempi;
        }
    }
}

export default Deck;



